<template>
  <div class="end" :class="{ modal_back : modalshow}" @click.self="$emit('closemodal')">
    <div class="end" :class="{ modal : modalshow}">
      <fa @click="$emit('closemodal')" icon="xmark" size="2xl" class="xicon" />
      <div class="inner-right" id="contact">
          <h1 class="title">제안서 다운로드</h1>
          <form @submit.prevent="submitForm">
            <div class="input-box">
              <input placeholder="회사명" v-model="form.company" required>
              <input type="text" v-model="form.name" placeholder="담당자 성함" required>
              <input type="text" v-model="form.department" placeholder="담당 부서" required >
              <input type="email" v-model="form.email" placeholder="e-mail" required >
            </div>
            <div class="check">
              <input type="checkbox" id="checkbox" required v-model="form.infochecked"><label for="checkbox">요청된 사항을 처리하기 위해 i-ESG가 위에 제출된 개인정보를 저장 및 처리하는 데 동의합니다.</label>
            </div>
            <a :href="`${publicPath}기후변화교육_소개자료_i-ESG.pdf`" download>
              <button type="submit">
                제안서 다운로드 >
              </button>
            </a>
          </form>
        </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  name : "DownloadModal",
  props : {
    modalshow : String,
  },
  data() {
    return {
      publicPath : process.env.BASE_URL,
      form: {
        company: '',
        name: '',
        department: '',
        email: '',
        infochecked: false,
      },
      submit : false,
    }
  },
  methods: {
    submitForm() {
      // this.validationCheck();
      // if (this.errors.length == 0) {
          axios.post('https://jsonplaceholder.typicode.com/posts', this.form)
              .then((res) => {
                  console.log(res.data)
                  this.adown()
                  this.showAlert()
              })
              .catch((err) => {
                  console.log(err)
              }).finally(() => {
                  //Perform action in always
              });
          this.clearAll();
      // }
    },
    clearAll() {
      this.form.name = '',
      this.form.email = '',
      this.form.company = '',
      this.form.department = '',
      this.form.infochecked = false
    },
    adown() {
      var a = document.createElement('a');
      a.style = 'display: none';
      a.href = this.publicPath + '기후변화교육_소개자료_i-ESG.pdf';
      a.download = '기후변화교육_소개자료_i-ESG.pdf';
      document.body.appendChild(a);
      a.click();

      setTimeout(function () {
        document.body.removeChild(a);
        URL.revokeObjectURL('i-esg-hp/public/기후변화교육_소개자료_i-ESG.pdf'); // 메모리 해제
      }, 100);
    },
    showAlert() {
      this.$swal({
        icon: 'success',
        title: '완료되었습니다.',
        text: '감사합니다.',
        confirmButtonColor: '#000116'
        });
    },
  }
}
</script>

<style scoped src="./contactModal.css">

