<br>
제3조 약관 외 준칙<br>
<br>
본 약관에 명시되지 않은 사항은 전기통신기본법, 전기통신사업법, 정보통신망 이용촉진 및 정보보호 등에 관한 법률, 정보통신윤리위원회 심의규정, 정보통신 윤리강령, 컴퓨터프로그램보호법 등 기타 대한민국의 관련법령 및 상관행을 따릅니다.<br>
<br>
제4조 용어의 정의<br>
<br>
본 약관에서 사용하는 용어의 정의는 다음 각 호와 같습니다.<br>
<br>
1) 서비스 배경<br>
회사가 온라인 플랫폼을 통해 제공하는 서비스는 빠르게 규제화되고 있는 ESG의 다양한 요구에 보다 쉽고, 효율적으로 관리/대응할 수 있도록 돕는 ESG 특화 디지털 솔루션입니다.<br>
2) 서비스 : <br>
 - i-ESG 홈페이지에서 기업고객으로하여금 원하는 서비스 또는 정보를 검색하여 이를 웹상에서 활용할 수 있도록 하는 웹상에서의 일련의 유료/무료 서비스 <br>
 - i-ESG 홈페이지에서 유료회원 가입 후, 회원사의 ESG 관리를 원할하게 하는 데 도움이 되는 웹상에서의 ESG 특화 모듈 (진단, 공시자동화, 대시보드, 공급망 관리 등) <br>
 - i-ESG 홈페이지에서 ESG 관련 유용한 정보 제공 등 추가 서비스 등 <br>
 - 기타 회사가 정한 서비스 배경에 부합하여 회사가 추가로 제공 또는 제공 예정인 서비스 <br>
3) 이용자 : i-ESG 서비스를 이용하는 모든 회원 및 비회원<br>
4) 회원 : 홈페이지에서 본 약관에 따라 회원가입 절차를 거친 후, 회사가 제공하는 모든 제반 서비스를 이용하는 자<br>
 - 기업고객 : 회원 중 유료 서비스를 구독하여 i-ESG 서비스를 이용하여 ESG 관리/대응을 진행하는 기업고객 <br>
본 약관에서 사용하는 용어의 정의는 제1항에서 정하는 것을 제외하고는 관계법령, 개별서비스에 대한 별도약관 및 이용규정에서 정하는 바에 의합니다.<br>
<br>
<br>
제 2 장 서비스 이용계약<br>
<br>
제5조 이용계약의 성립<br>
<br>
1) 이용계약은 이용자의 약관 내용에 대한 동의와 이용자의 이용신청에 대한 회사의 승낙으로 성립합니다.<br>
2) 이용계약에 대한 동의는 이용신청 당시 회사 이용약관의 “동의함”을 선택함으로써 의사표시를 합니다.<br>
<br>
제6조 이용신청 및 제한<br>
<br>
1) 회사는 신청서 양식에 정확히 기재하여 이용신청하였을 경우 서비스 이용신청을 승낙합니다.<br>
2) 회사는 다음 각 호에 해당하는 신청에 대해서는 승낙하지 않습니다.<br>
① 타인 동의 없이 타인명의로 서비스를 신청한 경우<br>
② 정보를 허위로 기재하였거나 허위의 서류를 제출한 경우<br>
③ 회사가 정한 이용신청 요건이 미비 되었을 경우(필수항목 누락, 오기 등)<br>
④ 법령 또는 약관을 위반하여 이용계약이 해지 된 적이 있는 이용자가 신청한 경우<br>
⑤ 사회의 안녕질서 혹은 미풍양속을 저해할 목적으로 신청한 경우<br>
⑥ 기타 부정한 용도로 본 서비스를 이용하고자 하는 경우<br>
3) 회사는 다음 각 호의 경우 그 사유가 해소될 때까지 서비스 이용 승인을 유보 또는 제한할 수 있습니다.<br>
① 서비스 관련 제반 용량이 부족한 경우<br>
② 회사의 기술상 장애 사유가 있는 경우<br>
<br>
제7조 회원ID 부여 및 변경 등<br>
<br>
1) 회사는 이용자에 대하여 약관이 정하는 바에 따라 회원에게 ID를 부여합니다.<br>
2) ID는 원칙적으로 변경이 불가하나, 다음 각 호와 같이 부득이한 사유로 변경하고자 하는 경우 회원 또는 회사의 요청으로 변경할 수 있습니다.<br>
① ID가 회원의 이메일, 전화번호 등으로 등록되어 사생활 침해가 우려되는 경우<br>
② 타인에게 혐오감을 주거나 미풍양속에 저해할 목적으로 신청한 경우<br>
③ 기타 합리적 사유가 있는 경우<br>
3) 회원탈퇴 후 해당 아이디는 영구적으로 사용이 중지되므로 해당 아이디로는 재가입이 불가능합니다.<br>
4) 회원은 회원정보 변경을 통해 언제든지 자신의 정보를 열람하고 수정할 수 있습니다.<br>
<br>
제8조 회원정보 사용에 대한 동의<br>
<br>
홈페이지의 회원정보는 다음과 같이 수집, 사용, 관리, 보호되며, 회원이 본 약관에 따라 이용신청을 하는 것은 회사가 신청서에 기재된 회원정보를 수집, 이용하는 것에 동의하는 것으로 간주됩니다.<br>
1) 회원정보의 수집 : 회사는 서비스 가입 시 회원이 제공하는 정보를 통해서 회원의 정보를 수집합니다.<br>
2) 회원정보의 사용 : 회사는 서비스 제공과 관련해서 수집된 회원의 신상정보를 본인의 승낙 없이 제3자에게 누설, 배포하지 않습니다. 단, 전기통신기본법 등 법률의 규정에 의해 국가기관의 요구가 있는 경우, 범죄에 대한 수사상의 목적이 있거나 정보통신윤리위원회의 요청이 있는 경우 또는 기타 관계법령에서 정한 절차에 따른 요청이 있는 경우, 회원이 홈페이지에 제공한 회원정보를 스스로 공개한 경우는 제외합니다.<br>
3) 회원정보의 관리 : 회원은 회원정보의 보호 및 관리를 위하여 수시로 귀하의 회원정보를 수정 또는 삭제할 수 있습니다. 수신되는 정보 중 불필요하다고 생각되는 부분도 변경 또는 조정 할 수 있습니다.<br>
4) 회원정보의 보호 : 회원정보는 오직 회원만이 열람•수정•삭제 할 수 있으며, 이는 전적으로 회원의 ID와 비밀번호에 의해 관리되고 있습니다. 따라서 타인에게 회원의 ID와 비밀번호를 알려주어서는 아니 되며, 작업 종료 시에는 반드시 로그아웃 하고, 웹 브라우저의 창을 닫아야 합니다.<br>
5) 회원정보의 수집 및 이용에 대해서는 관련 법령에 준수하여 i-ESG 홈페이지에 게재한 개인정보 취급방침에 의거 활용되며, 제3자향 정보 제공에 대한 사항은 이용자의 "동의함" 의사표시에 의거 향후 수집, 활용될 수 있습니다. <br>
<br>
제 3 장 서비스 제공 및 이용<br>
<br>
제9조 서비스의 개시 및 이용<br>
<br>
1) 회사는 회원의 이용신청을 승낙한 때부터 서비스를 개시합니다. 단, 일부 서비스의 경우에는 지정된 일자부터 서비스를 개시합니다.<br>
2) 회사의 업무상 또는 기술상의 장애로 인하여 서비스를 개시하지 못하는 경우에는 사이트에 공시하거나 회원에게 이를 통지합니다.<br>
3) 서비스의 이용은 연중무휴이며 1일 24시간을 원칙으로 합니다. 다만, 회사의 업무상 또는 기술상의 이유로 서비스가 일시 중지 될 수 있습니다. 이러한 경우 회사는 사전 또는 사후에 이를 공지합니다.<br>
4) 회원으로 가입한 이후라도 일부 서비스 이용 시 서비스 제공자의 요구에 따라 특정 회원에게만 서비스를 제공할 수 있습니다.<br>
5) 회사는 서비스를 일정 범위로 분할하여 각 범위별로 이용가능 시간을 별도로 정할 수 있습니다. 이러한 경우 회사는 사전에 이를 공지합니다.<br>
<br>
제10조 서비스의 중지<br>
<br>
1) 회원은 홈페이지에 보관되거나 전송된 메시지 및 기타 통신 메시지 등의 내용이 국가의 비상사태, 정전, i-ESG 관리범위 외의 서비스 설비 장애 및 기타 불가항력에 의하여 보관되지 못하였거나 삭제된 경우, 전송되지 못한 경우 및 기타 통신 데이터의 손실에 대해 회사는 관련 책임을 부담하지 않습니다.<br>
2) 회사가 정상적인 서비스 제공의 어려움으로 인하여 일시적으로 서비스를 중지하여야 할 경우에는 서비스 중지 1주일 전에 고지 후 서비스를 중지할 수 있으며, 이 기간 동안 회원이 고지 내용을 인지하지 못한데 대하여 회사는 책임을 부담하지 않습니다. 상당한 이유가 있을 경우 위 사전 고지기간은 감축되거나 생략될 수 있습니다. 또한 위 서비스 중지에 의하여 본 서비스에 보관되거나 전송된 메시지 및 기타 통신 메시지 등의 내용이 보관되지 못하였거나 삭제된 경우, 전송되지 못한 경우 및 기타 통신 데이터의 손실이 있을 경우에도 회사는 책임을 부담하지 않습니다.<br>
3) 회사의 사정으로 서비스를 영구적으로 중단하여야 할 경우에 제2항을 준용합니다. 다만 이 경우 사전 고지기간은 1개월로 합니다.<br>
4) 천재지변, 국가비상사태 등 정상적인 서비스 제공이 불가능할 경우 일시적으로 서비스를 제한, 정지시킬 수 있으며 사전 또는 사후 회원에게 중지사유 및 기간을 공지합니다.<br>
5) 회사는 사전 공지 후 서비스를 일시적으로 중단할 수 있으며, 이에 대하여 회원 또는 제3자에 어떠한 책임도 부담하지 않습니다.<br>
6) 회사는 긴급한 시스템 점검, 증설 및 교체 등 부득이한 사유로 인하여 예고 없이 일시적으로 서비스를 중단할 수 있으며, 새로운 서비스로의 교체 등 회사가 적절하다고 판단하는 사유에 의하여 현재 제공되는 서비스를 완전히 중단할 수 있습니다.<br>
7) 회사가 통제할 수 없는 사유로 인한 서비스 중단의 경우(시스템관리자의 고의, 과실 없는 디스크 장애, 시스템 다운 등)에 사전통지가 불가능 하며, 타인(PC통신회사, 기간통신사업자 등)의 고의, 과실로 인한 시스템 중단 등의 경우에는 통지하지 않습니다.<br>
8) 1년 이상 장기간 휴면 회원인 경우 안내 메일 또는 공지사항 발표 후 1주일간의 통지기간을 거쳐 서비스 사용을 중지할 수 있습니다.<br>
<br>
제11조 서비스 이용제한<br>
<br>
1) 회원이 제공하는 정보의 내용이 허위인 것으로 판명되거나, 허위가 있다고 의심할 만한 합리적인 사유가 발생할 경우 회원의 본 서비스 사용을 일부 또는 전부 이용 정지할 수 있으며, 이로 인해 발생하는 불이익에 대해 회사는 책임을 부담하지 않습니다.<br>
2) 회원이 본 약관 제16조(회원의 의무) 등 본 약관의 내용에 위배되는 행동을 한 경우, 임의로 서비스 사용을 제한 및 중지할 수 있습니다. 이 경우 회사는 회원의 접속을 금지할 수 있습니다.<br>
<br>
제12조 정보제공 및 광고의 게재<br>
<br>
1) 회사는 회원이 서비스 이용 중 필요하다고 인정되는 다양한 정보 및 광고에 대해서는 전자우편이나 서신우편, SMS(핸드폰 문자메시지), DM(Direct Mail), 메신저 등의 방법으로 회원에게 제공할 수 있으며, 만약 원치 않는 정보를 수신한 경우 회원은 이를 수신거부 할 수 있습니다.<br>
2) 회사는 서비스의 운용과 관련하여 서비스화면, 홈페이지, 전자우편 등에 광고 등을 게재할 수 있으며, 서비스를 이용하고자 하는 회원이 이 광고게재에 대하여 동의하는 것으로 간주합니다.<br>
3) 회사는 서비스 상에 게재되어 있거나 서비스를 통한 광고주와의 판촉활동에 회원이 참여하거나 교신 또는 거래의 결과로서 발생하는 모든 손실 또는 손해에 대해 책임을 지지 않습니다.<br>
<br>
제13조 게시물의 관리 운영<br>
<br>
1) 회원이 게시한 게시물의 저작권은 회원이 소유하며, 회사는 서비스 내에 이를 게시할 수 있는 권리를 갖습니다.<br>
2) 회사는 다음 각 호에 해당하는 게시물이나 자료를 사전 통지 없이 삭제하거나, 이동 또는 등록 거부를 할 수 있습니다.<br>
① 본 서비스 약관에 위배되거나 상용 또는 불법, 음란, 저속하다고 판단되는 게시물을 게시한 경우<br>
② 다른 회원 또는 제 3자를 비방하거나 중상모략으로 명예를 손상시키는 내용인 경우<br>
③ 공공질서 및 미풍양속에 위반되는 내용인 경우<br>
④ 범죄적 행위에 결부된다고 인정되는 내용일 경우<br>
⑤ 제3자의 저작권 등 기타 권리를 침해하는 내용인 경우<br>
⑥ 서비스 성격에 부합하지 않는 정보의 경우<br>
⑦ 허위 정보 기재(신분위장, 아이디어 침해 등을 목적으로 )를 등록한 경우<br>
⑧ 기타 관계 법령에 위배되는 경우<br>
3) 회사는 게시된 내용이 일정 기간 이상 경과되어 게시물로써의 효력을 상실하여 그 존치 목적이 불분명한 경우, 공지 발표 후 2주일간의 통지 기간을 거쳐 해당 게시물을 삭제할 수 있습니다.<br>
4) 회원의 게시물이 타인의 저작권을 침해함으로써 발생하는 민·형사상의 책임은 회원에게 있습니다.<br>
5) 회원은 서비스를 이용하여 얻은 정보를 가공, 판매하는 행위 등 서비스에 게재된 자료를 상업적으로 사용할 수 없습니다.<br>
<br>
제14조 회사의 소유권<br>
<br>
1) 회사가 제공하는 서비스, 그에 필요한 소프트웨어, 이미지, 마크, 로고, 디자인, 서비스 명칭, 정보 및 상표 등과 관련된 지적재산권 및 기타 권리는 회사에게 있습니다.<br>
2) 회원은 회사가 명시적으로 승인한 경우를 제외하고는 제1항 소정의 각 재산에 대한 전부 또는 일부의 수정, 대여, 대출, 판매, 배포, 제작, 양도, 재라이센스, 담보권설정행위, 상업적 이용행위를 할 수 없으며, 제3자로 하여금 이와 같은 행위를 하도록 허락할 수 없습니다.<br>
<br>
<br>
제 4 장 의무 및 책임<br>
<br>
제15조 회사의 의무<br>
<br>
1) 회사는 법령과 본 약관이 금지하거나 미풍양속에 반하는 행위를 하지 않으며, 지속적·안정적으로 서비스를 제공하기 위하여 노력할 의무가 있습니다.<br>
2) 회사는 회원의 개인 신상 정보를 본인의 승낙 없이 제3자에게 누설, 배포하지 않습니다. 다만, 관계법령에 의해 국가 기관의 요구나 전기통신관련법령 및 정보통신윤리위원회의 요청 등 법률의 규정에 따른 적법한 절차에 의한 경우는 제외합니다.<br>
3) 회사는 계속적이고 안정적인 서비스의 제공을 위하여 설비에 장애가 생기거나 멸실된 때에는 부득이한 사유가 없는 한 지체 없이 이를 수리 또는 복구합니다.<br>
4) 회사가 제공하는 서비스로 인하여 회원에게 손해가 발생한 경우 그러한 손해가 회사의 고의나 중과실에 기해 발생한 경우에 한하여 책임을 부담하며, 그 책임의 범위는 통상손해에 한합니다.<br>
5) 회사는 회원으로부터 제기되는 의견이나 불만이 정당하다고 인정할 경우에는 신속히 처리합니다. 다만, 신속한 처리가 곤란한 경우에는 회원에게 그 사유와 처리 일정을 통보합니다.<br>
6) 회사는 관련법령이 정하는 바에 따라서 회원 등록 정보를 포함한 회원의 개인 정보를 보호하기 위하여 노력합니다. 회원의 개인 정보 보호에 관해서는 관련법령 및 제 16조에 제시된 내용을 지킵니다.<br>
<br>
제16조 회원의 의무<br>
<br>
1) 회원은 서비스 이용을 위해 가입할 경우 현재의 사실과 일치하는 완전한 정보(이하 ‘가입정보’)를 제공해야 합니다. 또한 가입정보에 변경이 발생할 경우 즉시 갱신해야 합니다.<br>
2) 회원에게 부여되는 ID, 비밀번호는 다음 각 호와 같이 관리해야 합니다.<br>
① 회원의 승인 없이 ID나 비밀번호가 부정하게 사용되었다는 사실을 발견한 경우에는 즉시 회사에 신고해야 합니다.<br>
② 매 접속 종료 시에는 반드시 로그아웃 하고, 웹 브라우저의 창을 닫아야 합니다.<br>
3) 회원은 서비스를 이용하면서 다음과 같은 행위를 하지 않기로 동의합니다.<br>
① 타인(소수를 포함)에게 위해를 가하는 행위<br>
- 타인의 ID, 비밀번호, 명의 도용 및 타인으로 가장하는 행위<br>
- 타인과의 관계를 허위로 명시하는 행위<br>
- 타인을 비방할 목적으로 사실 또는 허위의 사실을 적시하여 명예를 훼손하는 행위<br>
- 본인 또는 타인에게 재산상의 이익을 주거나 손해를 가할 목적으로 허위의 정보를 유통시키는 행위<br>
- 수치심이나 혐오감 또는 공포심을 일으키는 말이나 음향, 글이나 화상 또는 영상을 계속하여 상대방에게 도달하게 해 상대방의 일상적 생활을 방해하는 행위<br>
- 회사의 사전 승낙 없이 서비스를 이용한 영리 행위<br>
② 불필요하거나 승인되지 않은 광고, 판촉물을 게재하는 행위<br>
③ 정크 메일(junk mail), 스팸(spam), 도배글, 피라미드 조직 등을 권유하거나 게시, 게재 또는 전자우편으로 보내는 행위<br>
④ 저속 또는 음란한 데이터, 텍스트, 소프트웨어, 음악, 사진, 그래픽, 비디오 메시지 등(이하 ‘컨텐츠’)을 게시, 게재 또는 전자우편으로 보내는 행위<br>
⑤ 지적재산권을 포함한 모든 권리가 없는 컨텐츠를 게시, 게재 또는 전자우편으로 보내는 행위<br>
⑥ 컴퓨터 소프트웨어, 하드웨어, 전기통신 장비를 파괴, 방해 또는 기능을 제한하기 위한 소프트웨어 바이러스를 게시, 게재 또는 전자우편으로 보내는 행위<br>
⑦ 다른 컴퓨터 코드, 파일, 프로그램을 포함하고 있는 자료를 게시, 게재, 전자우편으로 보내는 행위 등 다른 사용자의 개인정보를 수집 또는 저장하는 행위<br>
⑧ 재물을 걸고 도박하거나 사행 행위를 하는 행위<br>
⑨ 윤락 행위를 알선하거나 음행을 매개하는 내용의 정보를 유통시키는 행위<br>
⑩ 서비스의 안정적인 운영에 지장을 주거나 줄 우려가 있다고 판단되는 행위<br>
⑪ 기업고객 및 관련인이 거짓으로 위장하여 본인의 콘테스트에 참여하는 행위<br>
⑫ 기타 불법적이거나 부당한 행위<br>
4) 본 약관 및 관계법령에서 규정한 사항을 준수하지 않음으로 인해 발생하는 서비스 이용 상의 손해 또는 제3자에 의한 부정 이용 등에 대한 책임은 회원에게 있으며, 회사는 그에 대해 책임을 지지 않습니다.<br>
<br>
<br>
제17조 개인정보보호정책<br>
<br>
1) 회사는 이용신청 시 회원이 제공하는 정보를 통하여 회원에 관한 정보를 수집하며, 회원의 개인정보는 본 이용계약의 이행과 본 이용계약상의 서비스 제공을 위한 목적으로만 이용합니다.<br>
2) 회사는 정보통신망법 등 관계 법령이 정하는 바에 따라 회원의 개인정보를 보호하기 위해 노력합니다. 개인정보의 보호 및 사용에 대해서는 관련법 및 회사의 개인정보취급방침이 적용됩니다.<br>
3) 회사는 서비스 제공과 관련하여 취득한 회원의 정보를 본인의 승낙 없이 제3자에게 누설 또는 배포할 수 없으며 상업적 목적으로 사용할 수 없습니다. 다만, 다음의 각 호의 경우는 제외합니다.<br>
① 관계 법령에 의하여 수사상의 목적으로 관계기관으로부터 요구가 있는 경우<br>
② 정보통신윤리위원회의 요청이 있는 경우<br>
③ 기타 관계법령에서 정한 절차에 따른 요청이 있는 경우<br>
<br>
제18조 i-ESG 제휴 서비스 활용 및 관리에 대한 운영<br>
<br>
<br>
1) 다음 각 호에 해당하는 경우 회사는 해당 서비스에 대해 검토해볼 권리가 있습니다.<br>
① 기업고객이 i-ESG 연계 서비스 활용 시, 서비스 진행에 대한 현황 확인<br>
② 다른 회원의 신고가 접수된 경우<br>
③ 기타 부정행위로 의심할만한 근거가 발견된 경우<br>
2) 부정 행위 적발 시 기업고객에게 내용 통보 후 제휴 파트너사를 퇴출할 수 있습니다. <br>
① 이러한 경우, 회사는 기업고객의 ESG 관리가 용이하도록 타 제휴사를 소개하는 데 최선을 다할 의무가 있습니다. <br>
<br>
<br>
<br>
제 5 장 계약사항 변경 등<br>
<br>
제19조 계약해지 및 이용제한<br>
<br>
1) 회원이 이용계약을 해지하고자 할 때에는 회원 본인이 직접 인터넷을 통해 홈페이지에 해지 신청을 해야 합니다.<br>
2) 회사는 보안 및 아이디 정책, 서비스의 원활한 제공 등과 같은 이유로 회원의 ID 및 비밀번호 변경을 요구하거나 변경 할 수 있습니다.<br>
3) 회사는 회원이 다음 각 호에 해당하는 행위를 하였을 경우 사전통지 없이 이용계약을 해지할 수 있습니다.<br>
① 회원이 제공한 데이터가 허위임이 판명된 경우<br>
② 범죄적 행위에 관련되는 경우<br>
③ 국익 또는 사회적 공익을 저해할 목적으로 서비스 이용을 계획 또는 실행할 경우<br>
④ 타인의 서비스 아이디 및 비밀번호를 도용한 경우<br>
⑤ 타인의 명예를 손상시키거나 불이익을 주는 경우<br>
⑥ 같은 사용자가 다른 아이디로 이중 등록을 한 경우<br>
⑦ 서비스에 위해를 가하는 등 서비스의 건전한 이용을 저해하는 경우<br>
⑧ 기타 관련법령이나 회사가 정한 이용조건에 위배되는 경우<br>
4) 제16조 3항에 위배되는 행위 시 사전 통지 없이 이용계약을 해지하거나 또는 일정 기간 서비스 이용을 제한할 수 있습니다.<br>
5) 계약해지 시, 누적된 회원의 데이터는 모두 소멸됩니다.<br>
<br>
제20조 양도 금지<br>
<br>
1) 회원은 서비스의 이용권한, 기타 이용계약 상, 지위를 타인에게 양도 및 증여할 수 없으며, 이를 담보로 제공할 수 없습니다.<br>
<br>
<br>
제 6장 손해배상<br>
<br>
제21조 손해배상<br>
<br>
1) 서비스의 이용과정에서 알게 된 회원 개인정보를 상대방의 승인이 없는 한 부당하게 이를 이용하거나 제3자에게 누설하여서는 안됩니다.<br>
2) 서비스 이용기간 중, 상품계약 이후, 서비스 이용기간의 만료 또는 계약의 해제·해지 후에도 제1항의 이행의무가 있으며, 이에 위반하여 상대방에게 손해를 입힌 경우에는 이를 배상해야 합니다.<br>
3) 회사는 무료로 제공되는 서비스와 관련하여 귀책사유가 아닌 다른 사유에 의해 발생된 일체의 손해에 대한 책임을 지지 않습니다.<br>
<br>
제22조 면책조항<br>
<br>
1) 회사는 천재지변, 전쟁 및 기타 이에 준하는 불가항력으로 인하여 서비스를 제공할 수 없는 경우에는 서비스 제공에 대한 책임이 면제됩니다.<br>
2) 회사는 기간통신 사업자가 전기통신 서비스를 중지하거나 정상적으로 제공하지 아니하여 손해가 발생한 경우 책임이 면제됩니다.<br>
3) 회사는 서비스용 설비의 보수, 교체, 정기점검, 공사 등 부득이한 사유로 발생한 손해에 대한 책임이 면제됩니다.<br>
4) 회사는 이용자의 컴퓨터 오류에 의해 손해가 발생한 경우, 또는 회원이 신상정보 및 전자우편 주소를 부실하게 기재하여 손해가 발생한 경우 책임을 지지 않습니다.<br>
5) 회사는 회원 간 또는 회원과 제3자간에 서비스를 매개로 하여 물품거래 혹은 금전적 거래 등과 관련하여 어떠한 책임도 부담하지 아니하고, 회원이 서비스의 이용과 관련하여 기대하는 이익에 관하여 책임을 지지 않습니다.<br>
6) 회사는 회원 또는 기타 유관기관이 서비스에 게재한 의견이나 정보에 대해 정확도 및 신뢰도를 보장하지 않습니다. 이용자가 위 내용에 의존해 얻은 모든 종류의 이득이나 손해에 대하여 회사는 책임을 부담하지 않습니다.<br>
7) 회사는 이용자가 서비스 이용과 관련하여 가입자에게 발생한 손해 중 가입자의 고의, 과실에 의한 손해 및 타 이용자로 인해 입게 되는 정신적 피해에 대하여 보상할 책임을 지지 않습니다.<br>
<br>
<br>
<br>
제23조 재판관할<br>
<br>
1) 서비스 이용과 관련하여 회사와 회원 사이에 분쟁이 발생한 경우, 회사와 회원은 발생한 분쟁을 원만하게 해결하기 위하여 필요한 모든 노력을 해야 합니다.<br>
2) 제1항의 규정에도 불구하고 서비스 이용으로 발생한 분쟁에 대하여 소송이 제기될 경우, 회사 소재지를 관할하는 법원을 관할법원으로 합니다.<br>
<br>
* 부 칙<br>
1. 이 약관은 2022년 6월 1일부터 적용됩니다.<br>
